.form {
  padding-bottom: 15px;
  padding-top: 2px;

  @media (min-width: $tablets) {
    padding-bottom: 35px;
    padding-top: 2px;
  }

  @media (min-width: $desktops) {
    padding-bottom: 0;
    padding-top: 0;
  }
}

.form__content {
  display: flex;
  flex-direction: column;

  @media (min-width: $desktops) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.form__fieldset {
  margin: 0;
  padding: 34px 0 42px;
  border: none;
  position: relative;

  @media (min-width: $tablets) {
    position: relative;
    border: 2px solid $grey;
    border-radius: 2px;
    padding: 0;
    margin-bottom: 65px;
  }

  &--fullname {
    @media (min-width: $tablets) {
      order: -3;
      padding: 0;
      border: 0;
    }

    @media (min-width: $desktops) {
      margin-top: 4px;
      width: 435px;
    }
  }

  &--apps {
    background-color: $grey-light;

    @media (min-width: $tablets) {
      background-color: transparent;
      order: -2;
    }

    @media (min-width: $desktops) {
      width: 435px;
    }
  }

  &--contacts {
    padding-top: 30px;

    @media (min-width: $tablets) {
      order: -1;
      padding-top: 7px;
    }

    @media (min-width: $desktops) {
      width: 100%;
      padding-top: 0;
    }
  }

  &--achievements {
    background-color: $grey-light;

    @media (min-width: $tablets) {
      background-color: transparent;
    }

    @media (min-width: $desktops) {
      padding: 0;
      width: 100%;
    }
  }

  &--emotions {
    padding-bottom: 0;
    margin-bottom: 0;

    @media (min-width: $desktops) {
      width: 100%;
    }
  }
}

.form__fieldset-in {
  @media (max-width: $phones-max) {
    @include container-phones(20px);
  }
}

.form__title {
  padding: 0 30px;
  margin: 4px 0 16px;
  width: 100%;
  text-align: center;
  color: $pink;
  line-height: 18px;
  text-transform: uppercase;

  @media (min-width: $tablets) {
    padding: 0 32px;
    margin: -1px auto 0;
    width: auto;
    background-color: $white;
    transform: translateY(-50%);
  }

  &--contacts {
    @media (max-width: $phones-max) {
      display: none;
    }
  }

  &--apps {
    @media (min-width: $tablets) {
      padding: 0 24px;
    }
  }

  &--emotions {
    margin-bottom: 15px;

    @media (min-width: $tablets) {
      padding: 0 38px;
      margin-bottom: 0;
    }
  }
}

.form__text-fields {
  &--contacts {
    @media (min-width: $tablets) {
      padding: 30px 58px 0;
    }

    @media (min-width: $desktops) {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
      padding-top: 26px;
    }
  }
}

.form__field-text {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;

  &--fullname {
    @media (min-width: $tablets) {
      flex-direction: row;
      align-items: center;
      margin-bottom: 52px;
    }

    @media (min-width: $desktops) {
      margin-bottom: 54px;
    }
  }

  &--contacts {
    @media (min-width: $tablets) {
      position: relative;
      margin: 0;
      padding-bottom: 28px;
    }

    @media (min-width: $desktops) {
      width: 375px;
      padding-bottom: 13px;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.form__text-label {
  margin-left: 23px;
  text-transform: uppercase;
  color: $pink;
  line-height: 30px;

  &--fullname {
    @media (min-width: $tablets) {
      margin-left: 0;
      margin-right: 20px;
    }
  }

  &--contacts {
    @media (min-width: $tablets) {
      margin-left: 20px;
      margin-top: 10px;
      order: 2;

      &::before {
        content: "";
        position: absolute;
        top: 2px;
        right: 2px;
        width: 60px;
        height: 59px;
        background-color: $grey;
      }
    }
  }

  &--phone {
    @media (min-width: $tablets) {
      &::before {
        background-image: url("../img/icon-phone.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 20px 19px;
      }
    }
  }

  &--address {
    @media (min-width: $tablets) {
      &::before {
        background-image: url("../img/icon-mail.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: 18px 13px;
      }
    }
  }
}

.form__text-label + .form__text-input {
  margin-top: 6px;

  @media (min-width: $tablets) {
    margin-top: 0;
  }
}

.form__text-input {
  display: block;
  padding: 15px 20px 14px;
  width: 100%;
  border: 2px solid $grey;
  border-radius: 2px;
  font-family: $font;
  font-size: 18px;
  line-height: 30px;
  color: $font-color;
  outline: none;

  &:hover {
    border-color: $blue-dark;
  }

  &:active,
  &:focus {
    border-color: $pink;
  }

  &::placeholder {
    color: $font-color;
    opacity: 1;
  }

  &--contacts {
    padding-right: 62px;
  }
}

.form__checkbox-fields {
  padding-top: 23px;

  @media (min-width: $tablets) {
    padding: 34px 58px 48px;
  }

  @media (min-width: $desktops) {
    padding: 34px 58px 56px;
  }

  &--achievements {
    @media (min-width: $desktops) {
      display: flex;
      width: 100%;
    }
  }
}


.form__field-checkbox {
  position: relative;

  &:not(:last-child) {
    margin-bottom: 43px;
  }
}

.form__checkbox-wrapper {
  @media (max-width: $desktops) {
    &:not(:last-child) {
      margin-bottom: 43px;
    }
  }

  @media (min-width: $desktops) {
    flex-grow: 0;


    &:not(:last-child) {
      margin-right: 55px;
    }
  }
}

.form__checkbox-label {
  position: relative;
  display: inline-block;
  vertical-align: top;
  font-size: 18px;
  margin: 0;
  line-height: 30px;
  padding-top: 5px;
  padding-right: 0;
  padding-bottom: 4px;
  padding-left: 70px;
  cursor: pointer;

  @media (min-width: $tablets) {
    padding-left: 59px;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 39px;
    height: 39px;
    background-color: $white;
    border: 2px solid $grey;
    border-radius: 2px;
    z-index: 1;
  }

  &:hover::before {
    border-color: $blue-dark;
  }

  &:active::before {
    border-color: $pink;
  }
}

.form__checkbox-input:checked ~ .form__checkbox-label::after {
  content: "";
  position: absolute;
  top: 11px;
  left: 9px;
  width: 22px;
  height: 16px;
  background: url("../img/icon-check.svg") no-repeat top left;
  background-size: 22px 16px;
  z-index: 1;
}

.form__checkbox-input {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  width: 39px;
  height: 39px;
  z-index: 0;
  outline: none;
}

.form__radio-fields {
  padding-top: 20px;

  @media (min-width: $tablets) {
    padding: 35px 58px 48px;
  }

  &--apps {
    @media (min-width: $desktops) {
      padding: 33px 74px 44px;
    }
  }
}

.form__field-radio {
  position: relative;

  &:not(:last-child) {
    margin-bottom: 36px;
  }
}

.form__radio-label {
  position: relative;
  display: inline-block;
  vertical-align: top;
  font-size: 18px;
  margin: 0;
  line-height: 30px;
  padding-top: 5px;
  padding-right: 0;
  padding-bottom: 4px;
  padding-left: 70px;
  cursor: pointer;

  @media (min-width: $tablets) {
    padding-left: 59px;
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 39px;
    height: 39px;
    background-color: $white;
    border: 2px solid $grey;
    z-index: 1;
    border-radius: 50%;
  }

  &:hover::before {
    border-color: $blue-dark;
  }

  &:active::before {
    border-color: $pink;
  }
}

.form__radio-input {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  width: 39px;
  height: 39px;
  z-index: 0;
  outline: none;
}

.form__radio-input:checked ~ .form__radio-label::after {
  content: "";
  display: block;
  position: absolute;
  top: 12px;
  left: 12px;
  width: 15px;
  height: 15px;
  background-color: $pink;
  z-index: 2;
  border-radius: 50%;
}

.form__textarea-fields {
  @media (min-width: $tablets) {
    padding: 26px 58px 51px;
  }

  @media (min-width: $desktops) {
    padding-top: 25px;
    padding-bottom: 43px;
  }
}

.form__textarea {
  padding: 15px 20px 14px;
  margin: 0;
  display: block;
  border: 2px solid $grey;
  width: 100%;
  border-radius: 2px;
  font-family: $font;
  font-size: 18px;
  line-height: 30px;
  color: $font-color;
  outline: none;
  resize: none;

  &--emotions {
    height: 201px;

    @media (min-width: $tablets) {
      height: 193px;
    }

    @media (min-width: $desktops) {
      height: 128px;
    }
  }

  &:hover {
    border-color: $blue-dark;
  }

  &:active,
  &:focus {
    border-color: $pink;
  }

  &::placeholder {
    color: $font-color;
    opacity: 1;
  }
}

.form__button {
  display: flex;
  flex-direction: column;
  padding-top: 48px;

  @media (max-width: $phones-max) {
    @include container-phones(20px);
    text-align: center;
  }

  @media (min-width: $tablets) {
    flex-direction: row;
    align-items: center;
    padding-top: 69px;
    margin-bottom: 10px;
  }

  @media (min-width: $desktops) {
    padding-top: 62px;
    margin-bottom: 20px;
  }
}

.form__submit-btn {
  width: 100%;
  flex-shrink: 0;
  margin-right: 20px;

  @media (min-width: $tablets) {
    width: 300px;
  }
}

.form__note {
  margin-top: 35px;
  line-height: 30px;
  text-transform: uppercase;

  @media (min-width: $tablets) {
    margin-top: 0;
    margin-left: auto;
  }
}

.form__note-symbol {
  color: $pink;
}

.form__note-required {
  display: none;

  @media (min-width: $desktops) {
    display: inline;
  }
}
